// Main SCSS Document

/* ## Typographical Elements
--------------------------------------------- */

html {
	font-size: $html-font-size;

	/* 10px browser default */
}

/* Chrome fix */
body > div {
	font-size: $base-font-size-smaller;

	@include media($screen-s) {
		font-size: $base-font-size;
	}
}

body,
td,
th,
p {
	color: #333;
	font-size: 1.4rem;
	font-family: Arial, Helvetica, sans-serif;
	line-height: 1.5;
}

body {
	position: relative;
	box-sizing: border-box;
	margin: 0;
	font-size: 1.8rem;
	background-color: #fdfdfd;
}

ul,
li {
	color: #333;
	font-size: 1.8rem;
	font-family: Arial, Helvetica, sans-serif;
}

a {
	color: orange;
	text-decoration: none;
	border-bottom: 1px solid orange;
	transition: 0.3s all;
}

a:hover,
a:focus {
	color: #3397db;
	text-decoration: none;
	border-color: transparent;
}

a img {
	border: none 0 #fff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0 0 20px;
	font-family: Arial, Helvetica, sans-serif;
}

article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block;
}

#header {
	height: 130px;
}

#maincontent {
	width: 100%;
	min-height: 100%;
	margin-top: 0;
	overflow: hidden;
	background-color: #f3f3f3;
}

#footer {
	position: relative;
	display: block;
	width: auto;
	padding: 25px;
	overflow: hidden;
	color: #aaa;
	font-size: 1rem;
	letter-spacing: 1px;
	text-align: center;
	text-transform: uppercase;
	background-color: #444;
}

/* ====================== Navigation ====================== */
nav {
	display: flex;
	width: 100%;
	height: 80px;
	text-align: center;
	background-color: #252831;
}

nav h1,
nav h1 a {
	margin: auto;
	padding-bottom: 8px;
	padding-left: 10px;
	color: #fff;
	font-weight: 200;
	font-size: 18px;
	letter-spacing: 10px;
	text-transform: uppercase;
}

nav h1 a {
	border-bottom: 2px solid transparent;

	&:focus,
	&:hover {
		border-color: #3397db;
	}
}

#breadcrumb {
	position: relative;
	width: 100%;
	margin: 0;
	padding: 10px 40px 16px;
	font-size: 1.6rem;
	list-style: none;
	background-color: #eee;
}

/* Display list items side by side */
#breadcrumb li {
	display: inline;
}

/* Add a slash symbol (/) before/behind each list item */
#breadcrumb li + li::before {
	padding: 8px;
	color: black;
	content: '/\00a0';
}

/* Add a color to all links inside the list */
#breadcrumb li a {
	color: #0275d8;
	text-decoration: none;
	border-color: #3397db;
}

/* Add a color on mouse-over and focus */
#breadcrumb li a:hover,
#breadcrumb li a:focus {
	color: #01447e;
	border-color: transparent;
}

/* ====================== Map ====================== */
#map {
	width: 100%;
	min-height: 400px;
	background-color: #ccc;
}

/* ====================== Restaurant Filtering ====================== */
.filter-options {
	align-items: center;
	width: 100%;
	height: 100%;
	padding: 12px;
	background-color: #3397db;
}

.filter-options h2 {
	margin: 0 12px;
	color: white;
	font-weight: normal;
	font-size: 1.6rem;
	line-height: 1;
}

.filter-options select {
	width: 200px;
	height: 35px;
	margin: 10px;
	padding: 0 10px;
	font-size: 1.6rem;
	font-family: Arial, sans-serif;
	letter-spacing: 0;
	background-color: white;
	border: 1px solid #fff;
}

/* ====================== Restaurant Listing ====================== */
#restaurants-list {
	display: grid;
	// flex-flow: row wrap;
	// justify-content: flex-start;
	// align-items: center;
	max-width: 100%;
	margin: 0;
	padding: 30px 15px 60px;
	text-align: center;
	list-style: outside none none;
	background-color: #f3f3f3;
	grid-gap: 10px;
	grid-template-columns: repeat(auto-fill, minmax(360px, 1fr));
}

#restaurants-list li {
	flex: 0 0 360px;
	max-width: 470px;
	min-height: 380px;
	margin: 15px;
	padding: 0 30px 25px;
	font-family: Arial, sans-serif;
	text-align: left;
	background-color: #fff;
	border: 2px solid #ccc;
}

#restaurants-list .restaurant-img {
	display: block;
	width: calc(100% + 60px);
	min-width: 100%;
	max-width: none;
	margin: 0 0 0 -30px;
	background-color: #ccc;
}

#restaurants-list li h1 {
	margin: 20px 0 10px;
	color: #f18200;
	font-weight: 200;
	font-size: 1.6rem;
	font-family: Arial, sans-serif;
	line-height: 1.3;
	letter-spacing: 0;
	text-transform: uppercase;
}

#restaurants-list p {
	margin: 0;
	font-size: 1.4rem;
}

#reviews-container a,
#restaurants-list li a {
	display: inline-block;
	margin: 15px 0 0;
	padding: 8px 30px 10px;
	color: #fff;
	font-size: 1.2rem;
	text-align: center;
	text-transform: uppercase;
	text-decoration: none;
	background-color: orange;
	border: 3px solid transparent;

	&:hover,
	&:focus {
		background-color: #333;
		border-color: #d9d9d9;
	}
}

#reviews-container {
	display: flex;
	flex-flow: column wrap;

	a {
		margin: auto;
	}
}

/* ====================== Restaurant Details ====================== */
.inside header {
	position: fixed;
	top: 0;
	z-index: 1000;
	width: 100%;
	height: 130px;
}

.inside #maincontent {
	@include media($screen-m) {
		margin-top: 130px;
	}
}

.inside #map-container {
	position: relative;
	box-sizing: border-box;
	// top: 80px;
	// right: 0;
	width: 100%;
	height: 50vh;
	min-height: 50vh;
	background: transparent none repeat scroll 0 0;

	@include media($screen-m) {
		position: fixed;
		top: 120px;
		right: 0;
		width: 50%;
		min-height: 80vh;
		padding: 120px 40px 40px;
	}
}

.inside #map {
	width: 100%;
	height: 100%;
	background-color: #ccc;
}

// .inside #footer {
// 	position: relative;
// 	width: 100%;
// }

#restaurant-name {
	margin: 15px 0 30px;
	color: #f18200;
	font-weight: 200;
	font-size: 2rem;
	font-family: Arial, sans-serif;
	line-height: 1.1;
	letter-spacing: 0;
	text-transform: uppercase;
}

#restaurant-img {
	width: 100%;
	max-width: 800px;
}

#restaurant-address {
	margin: 10px 0;
	font-size: 1.8rem;
}

#restaurant-cuisine {
	width: 100%;
	max-width: 800px;
	margin: 0 0 20px;
	padding: 2px 0;
	color: #ddd;
	font-weight: 300;
	font-size: 1.8rem;
	letter-spacing: 10px;
	text-align: center;
	text-transform: uppercase;
	background-color: #333;
}

#restaurant-container,
#reviews-container {
	box-sizing: border-box;
	width: 100%;
	padding: 40px 40px 30px;
	border-top: 1px solid #fff;
	border-bottom: 1px solid #d9d9d9;

	@include media($screen-m) {
		width: 50%;
	}
}

#reviews-container {
	padding: 30px 40px 80px;
}

#reviews-container h2 {
	padding-bottom: 12px;
	color: #f58500;
	font-weight: 300;
	font-size: 2.4rem;
	letter-spacing: -1px;
}

#reviews-list {
	width: 100%;
	margin: 0;
	padding: 0;
}

#reviews-list li {
	position: relative;
	display: block;
	margin: 0 0 30px;
	padding: 20px;
	overflow: hidden;
	list-style-type: none;
	background-color: #fff;
	border: 2px solid #f3f3f3;
}

#reviews-list li p {
	margin: 0 0 10px;
}

#restaurant-hours td {
	color: #666;
}

.screen-reader-text {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	overflow: hidden;
	border: 0;
	clip: rect(0 0 0 0);
}

.screen-reader-text,
.screen-reader-text span,
.screen-reader-shortcut {
	position: absolute !important;
	width: 1px;
	height: 1px;
	overflow: hidden;
	color: #333;
	background: #fff;
	border: 0;
	clip: rect(0, 0, 0, 0);
}

.screen-reader-text:focus,
.screen-reader-shortcut:focus,
.genesis-nav-menu .search input[type='submit']:focus,
.widget_search input[type='submit']:focus {
	z-index: 100000;
	display: block;
	width: auto;
	height: auto;
	padding: 15px 23px 14px;
	font-weight: bold;
	font-size: 1em;

	/* Above WP toolbar. */
	text-decoration: none;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
}
